/*radio公共样式**/
.radio-inner {
  width: 100%;
  display: block;
  text-align: left;
  border-bottom: 1px solid #E6E6E6;
  height: 45px;
}
.radio-inner:last-child {
  border-bottom: none;
}
.radio-inner label {
  width: 100%;
  display: inline-block;
  padding: 10px 0;
  height: 25px;
  line-height: 25px;
}
.radio-text {
  font-size: 14px;
  color: #333333;
  float: left;
  font-weight: 400;
}
.a-radio {
  display: none;
}
.b-radio {
  display: inline-block;
  width: 22px;
  height: 22px;
  border-radius: 2px;
  vertical-align: middle;
  position: relative;
  border-radius: 50%;
  background-color: #FFFFFF;
  float: right;
  border: 1px solid #5177FC;
}
.b-radio:before {
  content: '';
  font-size: 0;
  width: 12px;
  height: 12px;
  background: #5177FC;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -6px;
  margin-top: -6px;
  border-radius: 50%;
  display: none;
}
.a-radio:checked ~ .b-radio:before {
  display: block;
}
/*footer*/
.copy {
  position: absolute;
  bottom: 0;
  padding: 10px;
  text-align: center;
  left: 0;
  font-size: 12px;
  right: 0;
  color: #999999;
}
/* 导航栏样式 */
.nav {
  background-color: #5177FC;
  height: 44px;
  color: #FFFFFF;
  font-size: 16px;
  text-align: center;
  line-height: 44px;
}
.nav > h4 {
  color: #FFFFFF;
  font-size: 18px;
  text-align: center;
  line-height: 44px;
  display: inline-block;
  font-weight: 400;
}
.nav-right {
  display: inline-block;
  float: right;
  height: 100%;
  padding: 0 15px;
}
.nav-cion {
  width: 20px;
  height: 15px;
}
.nav-list {
  position: fixed;
  top: 44px;
  right: 0;
  background: #ce9e9e;
  width: 240px;
  z-index: 999;
}
.nav-list span {
  font-size: 14px;
  color: #333333;
}
.nav-list span.active {
  color: red;
}
.nav-item {
  height: 45px;
  display: block;
  padding: 0 15px;
  text-decoration: none;
  text-align: left;
}
.nav-child-item {
  padding: 0 20px;
  -webkit-transform: perspective(400) rotate3d(1, 0, 0, -90deg);
  -webkit-transform-origin: 50% 0;
  -webkit-transition: 350ms;
  transition: 350ms;
}
.nav-pic-box {
  height: 100%;
  display: inline-block;
}
.nav-pic {
  width: 16px;
  height: 17px;
}

