/**滑块*/
.wrapper {
	width: 100%;
	height: 100%;
}
.swiper-inner{
	height: 200px;
	width: 100%;
}
.wrapper .item {
	height: 100%;
	width: 100%;
	overflow: hidden;
}
.dot-inner{
	    position: absolute;
    /* bottom: 15px; */
    margin-top: -32px;
    left: 0;
    right: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.dot {
	width: 8px;
	height: 8px;
	border-radius: 50%;
	-moz-border-radius: 50%;
	-webkit-border-radius: 50%;
	float: left;
	margin-right: 5px;
	border: 1px solid red;
}
.dot.active{
	background-color: red;
	animation: myfirst 1s;
	-moz-animation: myfirst 1s;	/* Firefox */
	-webkit-animation: myfirst 1s;	/* Safari 和 Chrome */
	-o-animation: myfirst 1s;	/* Opera */
}
@keyframes myfirst
{
from {background: rgba(255, 0, 0, 0.5);}
to {background: red;}
}
@-webkit-keyframes myfirst /* Safari 和 Chrome */
{
from {background: rgba(255, 0, 0, 0.5);}
to {background: red;}
}
